    import { mapState ,mapMutations } from 'vuex'; 
	export default {
		props:{
			itemData:{
				type:Object
			}
		},
		data() {
			return {
				
			}
		},
        computed: {
			...mapState(['hasLogin','userInfo'])
		},
		methods: {
			navTo(url) {
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				uni.navigateTo({
					url:url
				})
			},
			copyContent(){
				uni.setClipboardData({ data:this.itemData.invite_code, 
				success: (r=>{
					uni.showToast({
						title: '复制成功',
						duration: 2000
					});
				}),
				fail:(r=>{
					uni.showToast({
					    title: '复制失败',
					    duration: 2000
					});
				}), complete:(r=>{
					
				})})
			},
		}
	}
